	module.exports = {"layout":"post","title":"How to make blinking/flashing text with CSS3 and jQuery","description":"How to make blinking/flashing text with CSS3 and jQuery","date":"2014-10-27 17:54:00 +0530","category":"Snippets","coverimage":"js.jpg","body":"<p>Blinking text was one of the most dreaded homepage effects in the 90s. It was easily achieved by using the <code>&lt;blink&gt;</code> tag and was viewed upon as a real newbie thing to do. The <code>&lt;blink&gt;</code> tag is now a deprecated HTML element, but in the name of moving presentation/style to CSS3 and jQuery, there is now a way to achive it.</p>\n<h2>Using CSS3</h2>\n<p><strong>CSS:</strong></p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-class\">.blink_me</span> {\n    <span class=\"hljs-attribute\">-webkit-animation-name</span>: blinker;\n    <span class=\"hljs-attribute\">-webkit-animation-duration</span>: <span class=\"hljs-number\">1s</span>;\n    <span class=\"hljs-attribute\">-webkit-animation-timing-function</span>: linear;\n    <span class=\"hljs-attribute\">-webkit-animation-iteration-count</span>: infinite;\n\n    <span class=\"hljs-attribute\">-moz-animation-name</span>: blinker;\n    <span class=\"hljs-attribute\">-moz-animation-duration</span>: <span class=\"hljs-number\">1s</span>;\n    <span class=\"hljs-attribute\">-moz-animation-timing-function</span>: linear;\n    <span class=\"hljs-attribute\">-moz-animation-iteration-count</span>: infinite;\n\n    <span class=\"hljs-attribute\">animation-name</span>: blinker;\n    <span class=\"hljs-attribute\">animation-duration</span>: <span class=\"hljs-number\">1s</span>;\n    <span class=\"hljs-attribute\">animation-timing-function</span>: linear;\n    <span class=\"hljs-attribute\">animation-iteration-count</span>: infinite;\n}\n\n@-<span class=\"hljs-keyword\">moz</span>-<span class=\"hljs-keyword\">keyframes</span> blinker {  \n    0% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">1.0</span>; }\n    50% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">0.0</span>; }\n    100% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">1.0</span>; }\n}\n\n@-<span class=\"hljs-keyword\">webkit</span>-<span class=\"hljs-keyword\">keyframes</span> blinker {  \n    0% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">1.0</span>; }\n    50% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">0.0</span>; }\n    100% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">1.0</span>; }\n}\n\n@<span class=\"hljs-keyword\">keyframes</span> blinker {  \n    0% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">1.0</span>; }\n    50% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">0.0</span>; }\n    100% { <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">1.0</span>; }\n}\n</code></pre>\n<p><strong>HTML:</strong></p>\n<pre><code class=\"language-HTML\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">span</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"blink_me\"</span>&gt;</span>This Will Blink<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">span</span>&gt;</span>\n</code></pre>\n<p><a href=\"http://jsfiddle.net/umz8t/328/\" class=\"\" target=\"_blank\">Demo</a></p>\n<p><strong>This wont work on older versions of Internet Explorer, for that, you need to use jQuery.</strong></p>\n<h2>Using jQuery</h2>\n<p><strong>Javascript:</strong></p>\n<pre><code class=\"language-JavaScript\"><span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">blinker</span>(<span class=\"hljs-params\"></span>) </span>{\n    $(<span class=\"hljs-string\">'.blink_me'</span>).fadeOut(<span class=\"hljs-number\">500</span>);\n    $(<span class=\"hljs-string\">'.blink_me'</span>).fadeIn(<span class=\"hljs-number\">500</span>);\n}\n\nsetInterval(blinker, <span class=\"hljs-number\">1000</span>); <span class=\"hljs-comment\">//Runs every second</span>\n</code></pre>\n<p><strong>HTML:</strong></p>\n<pre><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">span</span> <span class=\"hljs-attr\">class</span>=<span class=\"hljs-string\">\"blink_me\"</span>&gt;</span>This Will Blink<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">span</span>&gt;</span>\n</code></pre>\n<p><a href=\"http://jsfiddle.net/umz8t/458/\" class=\"\" target=\"_blank\">Demo</a></p>\n"}

