	module.exports = {"layout":"post","title":"Lazy Load Image","description":null,"date":"2014-09-08 21:22:32 +0530","category":"snippets","tags":["javascript","jquery","lazy load","performance","plugin"],"body":"<p>This plugin is very useful and it boosts performance delaying loading of images in long web pages because images outside of viewport (visible part of web page) wont be loaded until the user scrolls to them.</p>\n<h3>Browser support</h3>\n<p>Compatible with All Browsers and IE7+.</p>\n<h3>Usage</h3>\n<p>Use a placeholder image in the src attribute - something to be displayed while the original image loads - and include the actual image source in a data-src attribute.</p>\n<p>If you want to serve high-resolution images to devices with retina displays, you just have to include the source for those images in a data-src-retina attribute.</p>\n<pre><code>&lt;<span class=\"hljs-selector-tag\">img</span> src=<span class=\"hljs-string\">\"bg.png\"</span> data-src=<span class=\"hljs-string\">\"img1.jpg\"</span> /&gt;\n&lt;<span class=\"hljs-selector-tag\">img</span> src=<span class=\"hljs-string\">\"bg.png\"</span> data-src=<span class=\"hljs-string\">\"img2.jpg\"</span> data-src-retina=<span class=\"hljs-string\">\"img2-retina.jpg\"</span> /&gt; \n</code></pre>\n<p>If you care about users without javascript enabled, you can include the original image inside a <code>&lt;noscript&gt;</code> tag:</p>\n<pre><code><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">noscript</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">img</span> <span class=\"hljs-attr\">src</span>=<span class=\"hljs-string\">\"img1.jpg\"</span> /&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">noscript</span>&gt;</span>\n</code></pre>\n<p>Run the script on document ready:</p>\n<pre><code>$(<span class=\"hljs-built_in\">document</span>).ready(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span>(<span class=\"hljs-params\"></span>) </span>{\n  $(<span class=\"hljs-string\">\"img\"</span>).unveil();\n});\n</code></pre>\n<h3>Threshold</h3>\n<p>By default, images are only loaded and unveiled when the user scrolls to them and they became visible on the screen.</p>\n<p>If you want your images to load earlier than that, lets say 200px before they appear on the screen, you just have to:</p>\n<pre><code><span class=\"hljs-variable\">$(</span><span class=\"hljs-string\">\"img\"</span>).unveil(<span class=\"hljs-number\">200</span>);\n</code></pre>\n<h3>Callback</h3>\n<p>As a second parameter you can also specify a callback function that will fire after an image as been unveiled.</p>\n<p>Inside the callback function this refers to the images DOM node, so with the help of CSS3 (or jQuery animations) and by attaching a simple load event you can easily add some fancy entrance animation to your images:</p>\n<pre><code class=\"language-css\"><span class=\"hljs-selector-tag\">img</span> {\n  <span class=\"hljs-attribute\">opacity</span>: <span class=\"hljs-number\">0</span>;\n  <span class=\"hljs-attribute\">transition</span>: opacity .<span class=\"hljs-number\">3s</span> ease-in;\n} \n</code></pre>\n<pre><code class=\"language-javascript\">$(<span class=\"hljs-string\">\"img\"</span>).unveil(<span class=\"hljs-number\">200</span>, <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span>(<span class=\"hljs-params\"></span>) </span>{\n  $(<span class=\"hljs-keyword\">this</span>).load(<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span>(<span class=\"hljs-params\"></span>) </span>{\n    <span class=\"hljs-keyword\">this</span>.style.opacity = <span class=\"hljs-number\">1</span>;\n  });\n});\n</code></pre>\n<h3>Trigger</h3>\n<p>You can still trigger image loading whenever you need.</p>\n<p>All you have to do is select the images you want to unveil and trigger the event:</p>\n<pre><code><span class=\"hljs-variable\">$(</span><span class=\"hljs-string\">\"img\"</span>).trigger(<span class=\"hljs-string\">\"unveil\"</span>);\n</code></pre>\n<h2>Demo and Download</h2>\n<p><a href=\"http://www.kapadiya.net/demo/lazy/\" class=\"\" target=\"_blank\">Demo</a> <a href=\"http://www.kapadiya.net/demo/lazy/lazy.rar\" class=\"\" target=\"_blank\">Download</a></p>\n"}

