	module.exports = {"layout":"post","title":"Install Jekyll on Windows","description":null,"headline":null,"date":"2014-08-12 16:26:02 +0530","category":"Webdevelopment","tags":["jekyll","ruby","windows"],"body":"<figure>\n\t<img src=\"jekyll.jpg\">\n</figure>\n<h2>The Easy Method</h2>\n<p>The easy way is to download <a href=\"http://railsinstaller.org/\" target=\"_blank\" rel=\"noopener noreferrer\">Railsinstaller</a>. It sets up ruby, rails, bundler and other packages in <code>C:\\Railsinstaller.</code> (Note: since I already have <code>git</code> installed elsewhere, I unchecked the install dialog box for <code>git</code> and <code>ssh</code>.)</p>\n<p>Once youve got Railsinstaller up and running, open powershell/cmd/gitshell and run:</p>\n<pre><code><span class=\"hljs-built_in\">cd</span> C:/Railsinstaller \ngem install jekyll\n</code></pre>\n<h2>Advance Method</h2>\n<h3>Install Ruby</h3>\n<ul>\n<li>\n<p>Go to <a href=\"http://rubyinstaller.org/downloads/\" target=\"_blank\" rel=\"noopener noreferrer\">http://rubyinstaller.org/downloads/</a></p>\n</li>\n<li>\n<p>In RubyInstallers section, click a version to download.<br>\nFor example, Ruby 2.0.0-p451 (x64) is the Windows installer for Ruby 2.0.0 x64 on 64-bit machines.</p>\n</li>\n<li>\n<p>Install through the installer</p>\n<ul>\n<li>Keep the default directory <code>C:\\Ruby200-x64</code> if possible, please note installer advises that Please avoid any folder name that contains spaces (e.g. Program Files).</li>\n<li>Tick Add Ruby executables to your PATH checkbox, so PATH will be updated automatically to avoid headaches.</li>\n</ul>\n  <figure><img src=\"ruby.png\"></figure>\n</li>\n<li>\n<p>Open up a command prompt window and type in the following command, to see if Ruby has been install correctly or not.</p>\n<blockquote>\n<p>ruby -v</p>\n</blockquote>\n<p>Example output:</p>\n<blockquote>\n<p>ruby 2.1.2p95 (2014-05-08 revision 45877) [x86_64-darwin13.0]</p>\n</blockquote>\n</li>\n</ul>\n<h3>Install DevKit</h3>\n<p>The DevKit is a toolkit that makes it easy to build and use native C/C++ extensions such as RDiscount and RedCloth for Ruby on Windows. Detailed installation instructions can be found on its <a href=\"https://github.com/oneclick/rubyinstaller/wiki/Development-Kit#installation-instructions\" target=\"_blank\" rel=\"noopener noreferrer\">wiki page</a>.</p>\n<ul>\n<li>\n<p>Go to <a href=\"http://rubyinstaller.org/downloads/\" target=\"_blank\" rel=\"noopener noreferrer\">http://rubyinstaller.org/downloads/</a> again.</p>\n</li>\n<li>\n<p>Download DEVELOPMENT KIT installer that matches the Windows architecture and the Ruby version just installed.<br>\nFor instance, DevKit-mingw64-64-4.7.2-20130224-1432-sfx.exe is for 64-bit Windows with Ruby 2.0.0 x64.<br>\nHere is a list about how to choose the correct DevKit version:</p>\n</li>\n</ul>\n<blockquote>\n<p><strong>Ruby 1.8.6 to 1.9.3</strong>: DevKit tdm-32-4.5.2<br /><br>\n<strong>Ruby 2.0.0</strong>: DevKit mingw64-32-4.7.2<br /><br>\n<strong>Ruby 2.0.0 x64</strong>: DevKit mingw64-64-4.7.2</p>\n</blockquote>\n<ul>\n<li>Run the installer and extract it to a folder, e.g. <code>C:\\DevKit</code>.</li>\n</ul>\n<blockquote>\n<p>cd C:\\DevKit<br>\nruby dk.rb init<br>\nnotepad config.yml</p>\n</blockquote>\n<ul>\n<li>\n<p>In opened notepad window, add a new line - <code>C:\\Ruby200-x64</code> at the end, save and close.</p>\n</li>\n<li>\n<p>Back to the Command Prompt, review (optional) and install.</p>\n</li>\n</ul>\n<blockquote>\n<p>ruby dk.rb review<br>\nruby dk.rb install</p>\n</blockquote>\n<h3>Install Jekyll</h3>\n<ul>\n<li>Verify that gem has been installed properly</li>\n</ul>\n<blockquote>\n<p>gem -v</p>\n</blockquote>\n<p>Example output:</p>\n<blockquote>\n<p>2.0.14</p>\n</blockquote>\n<ul>\n<li>Install Jekyll gem</li>\n</ul>\n<blockquote>\n<p>gem install jekyll</p>\n</blockquote>\n<h3>Install Pygments</h3>\n<p>The default syntax highlighting engine in Jekyll is Pygments. It requires Python to be installed and highlighter field to be set to pygments in sites configuration file _config.yml.</p>\n<p>Recently, Jekyll has added another highlighting engine called Rouge, which doesnt support as many languages as Pygments at the moment, but its Ruby native and Python-free. More details can be followed here.</p>\n<h4>Install Python</h4>\n<ol>\n<li>Go to <a href=\"http://www.python.org/download/\" target=\"_blank\" rel=\"noopener noreferrer\">http://www.python.org/download/</a></li>\n<li>Download appropriate version of Python windows installer, e.g. <strong>Python 2.7.6 Windows Installer</strong>. Note that Python 2 is preferred since Python 3 might not be functioning as intended at the time of writing.</li>\n<li>Install</li>\n<li>Set the installation directory (e.g. C:\\Python27) to PATH. (How to? See <a href=\"#troubleshooting\" target=\"_blank\" rel=\"noopener noreferrer\">Troubleshooting #1</a>)</li>\n<li>Verify Python installation</li>\n</ol>\n<blockquote>\n<p>python --version</p>\n</blockquote>\n<p>Example output:</p>\n<blockquote>\n<p>Python 2.7.6</p>\n</blockquote>\n<h4>Install pip</h4>\n<ul>\n<li>Download <a href=\"https://bootstrap.pypa.io/get-pip.py\" target=\"_blank\" rel=\"noopener noreferrer\">get-pip.py</a> via the link on that site.</li>\n<li>Next, open your favorite command line tool and navigate to the location of <a href=\"http://get-pip.py\" target=\"_blank\" rel=\"noopener noreferrer\">get-pip.py</a> on your computer (e.g., <code>C:\\pip\\</code>or whatever folder you saved it into).</li>\n</ul>\n<blockquote>\n<p>cd C:\\pip</p>\n</blockquote>\n<ul>\n<li>Then, run the following command to automagically download and install all required components.</li>\n</ul>\n<blockquote>\n<p>python <a href=\"http://get-pip.py\" target=\"_blank\" rel=\"noopener noreferrer\">get-pip.py</a></p>\n</blockquote>\n<h4>Install Pygments</h4>\n<ul>\n<li>From the command line, run the following command to install the Python base of Pygments.</li>\n</ul>\n<blockquote>\n<p>python -m pip install Pygments</p>\n</blockquote>\n<h2>Start Jekyll</h2>\n<p>Following the commands on official <a href=\"http://jekyllrb.com/docs/quickstart/\" target=\"_blank\" rel=\"noopener noreferrer\">Jekyll Quick-start guide</a>, a new Jekyll blog should be created and can be browsed at <a href=\"http://localhost:4000/\" target=\"_blank\" rel=\"noopener noreferrer\">localhost:4000</a>.</p>\n<blockquote>\n<p>jekyll new myblog<br>\ncd myblog<br>\njekyll serve</p>\n</blockquote>\n<h2>Troubleshooting</h2>\n<h3>Error message:</h3>\n<blockquote>\n<p>python is not recognized as an internal or external command, operable program or batch file.</p>\n</blockquote>\n<p><strong>Alternatives</strong>: python here can also be ruby, gem or easy_install, etc.</p>\n<p><strong>Reason</strong>: Program is not properly installed, or the PATH has not been set correctly.</p>\n<p><strong>Solution</strong>: Ensure the program has been installed correctly. Then add it to the PATH manually, see <a href=\"http://stackoverflow.com/questions/6318156/adding-python-path-on-windows-7/6318188#6318188\" target=\"_blank\" rel=\"noopener noreferrer\">this link</a>.</p>\n<h3>Error message:</h3>\n<blockquote>\n<p>ERROR:  Error installing jekyll:<br>\nERROR: Failed to build gem native extension.<br>\nC:/Program Files/Ruby/Ruby200-x64/bin/ruby.exe extconf.rb</p>\n</blockquote>\n<p><strong>Reason</strong>: Ruby has been installed to a folder with spaces.</p>\n<p><strong>Solution</strong>: Re-install Ruby, but this time install it to a folder without spaces, or simply keep the default directory when installing.</p>\n<h3>Error message:</h3>\n<blockquote>\n<p>Generating Liquid Exception: No such file or directory - python c:/Ruby200-x64/lib/ruby/gems/2.0.0/gems/pygments.rb-0.4.2/lib/pygments/mentos.py in <a href=\"http://2013-04-22-hello-world.md\" target=\"_blank\" rel=\"noopener noreferrer\">2013-04-22-hello-world.md</a></p>\n</blockquote>\n<p><strong>Reason</strong>: Pygments is not properly installed or the PATH is yet to be effective.</p>\n<p><strong>Solution</strong>: First make sure Pygments has been installed and PATH for Python is correct without no spaces or trailing slash. Then restart Command Prompt. If its not working, try logout Windows and log back in again. Or even try the ultimate and most powerful solution - turning the computer off and on again.</p>\n<h3>Error message:</h3>\n<blockquote>\n<p>Generating Liquid Exception: No such file or directory - /bin/sh in _posts/2013-04-22-hello-world.md</p>\n</blockquote>\n<p><strong>Reason</strong>: Incompatibility issue with pygments.rb versions 0.5.1/0.5.2.</p>\n<p><strong>Solution</strong>: Downgrade pygments.rb gem from 0.5.1/0.5.2 to version 0.5.0.</p>\n<blockquote>\n<p>gem uninstall pygments.rb --version 0.5.2<br>\ngem install pygments.rb --version 0.5.0</p>\n</blockquote>\n<h3>Error message:</h3>\n<blockquote>\n<p>c:/Ruby200-x64/lib/ruby/site_ruby/2.0.0/rubygems/core_ext/kernel_require.rb:55:in `require: cannot load such file  wdm (LoadError)</p>\n</blockquote>\n<p><strong>Reason</strong>: wdm gem could not be found. Since Jekyll only supports *nix systems officially, Windows Directory Monitor is therefore not included among Jekyll dependencies.</p>\n<p><strong>Solution</strong>:</p>\n<blockquote>\n<p>gem install wdm</p>\n</blockquote>\n"}

