	module.exports = {"layout":"post","title":"Removing unused CSS using Grunt ","description":null,"date":"2014-10-05 14:01:57 +0530","category":"Webdevelopment","tags":["grunt","css"],"coverimage":"grunt-logo.png","body":"<p>If you arent familiar with <strong>Grunt</strong>, it is a JavaScript task runner that you can use to automate pretty much anything in your development workflow. It works together with <strong>Node.js</strong> and there are tons of great plugins available.</p>\n<img src=\"grunt-logo.png\" class=\"center\" alt=\"\" width=\"200\" height=\"200\">\n<p>In this post, I am going to run through the process of setting up <strong>Node.js</strong> on a Windows machine, installing <strong>Grunt</strong> and getting the <strong>UnCSS</strong> plugin running on a simple project. By the time you get to the end of this post, you should be able to automatically remove unused CSS from your stylesheets as well as minify them on the fly. Youll be pretty impressed with the results that we achieve!</p>\n<h2>The Example Project</h2>\n<p>For this example, I am using a simple HTML page that uses the Twitter Bootstrap CSS as a template plus some custom CSS. The combined weight of the CSS files is <strong>124 KB</strong>.</p>\n<h2>Installing Node.js</h2>\n<p>In order to get Grunt running on your machine, you will need to install <a href=\"http://nodejs.org/\" target=\"_blank\" rel=\"noopener noreferrer\">Node.js</a>. Start by heading over to the <a href=\"http://nodejs.org/\" target=\"_blank\" rel=\"noopener noreferrer\">Node.js</a> website and download the latest distributable. It is easy to install and only takes a couple of minutes.</p>\n<h2>Installing Grunt</h2>\n<p>Next up, you will need to install Grunt. If you havent used <a href=\"http://gruntjs.com/\" target=\"_blank\" rel=\"noopener noreferrer\">Grunt</a> before, be sure to check out the <a href=\"http://gruntjs.com/getting-started\" target=\"_blank\" rel=\"noopener noreferrer\">Getting Started guide</a>, as it is a useful guide to installing this and many other Grunt plugins. In order to install Grunt, fire up the Node.js command prompt that you have just installed. In the command prompt, type the following:</p>\n<p><strong>npm install -g grunt-cli</strong></p>\n<p>his will download and install Grunt for you - easy! Next, navigate to the folder that contains your source files that you wish to process. In my case, the command I type is:</p>\n<p><strong>cd C:\\Dev\\ExampleProject</strong></p>\n<p>With the Node.js command prompt still open, type in the following code:</p>\n<p><strong>npm install grunt-uncss grunt-contrib-cssmin --save-dev</strong></p>\n<p>This command will install the <strong>UnCSS</strong> package and the <strong>CSSMin</strong> package in your project folder. You will only need to do this the first time you configure your project. Before going any further, we need to create a <strong>Grunt.js</strong> file in the root of the project that we are working on. The <strong>Gruntfile.js</strong> is a valid JavaScript file that belongs in the root directory of your project. Think of it as a configuration file that you use to configure the different plugins, as well as the order that they run in. The <strong>Gruntfile.js</strong> should look something similar to the code below.</p>\n<pre><code class=\"language-JavaScript\"><span class=\"hljs-built_in\">module</span>.exports = <span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> (<span class=\"hljs-params\">grunt</span>) </span>{\n\n    grunt.initConfig({\n        <span class=\"hljs-attr\">uncss</span>: {\n            <span class=\"hljs-attr\">dist</span>: {\n                <span class=\"hljs-attr\">files</span>: [\n                    { <span class=\"hljs-attr\">src</span>: <span class=\"hljs-string\">'index.html'</span>, <span class=\"hljs-attr\">dest</span>: <span class=\"hljs-string\">'cleancss/tidy.css'</span> }\n                ]\n            }\n        },\n        <span class=\"hljs-attr\">cssmin</span>: {\n            <span class=\"hljs-attr\">dist</span>: {\n                <span class=\"hljs-attr\">files</span>: [\n                    { <span class=\"hljs-attr\">src</span>: <span class=\"hljs-string\">'cleancss/tidy.css'</span>, <span class=\"hljs-attr\">dest</span>: <span class=\"hljs-string\">'cleancss/tidy.css'</span> }\n                ]\n            }\n        }\n    });\n\n    <span class=\"hljs-comment\">// Load the plugins</span>\n    grunt.loadNpmTasks(<span class=\"hljs-string\">'grunt-uncss'</span>);\n    grunt.loadNpmTasks(<span class=\"hljs-string\">'grunt-contrib-cssmin'</span>);\n\n    <span class=\"hljs-comment\">// Default tasks.</span>\n    grunt.registerTask(<span class=\"hljs-string\">'default'</span>, [<span class=\"hljs-string\">'uncss'</span>, <span class=\"hljs-string\">'cssmin'</span>]);\n\n};\n</code></pre>\n<p>In the code above, I am configuring the <strong>UnCSS</strong> plugin to look at my index.html file, locate any CSS and spit out a version with all the unused CSS removed. The new CSS file will be created in a new folder called cleancss. Next, the <strong>CSSmin</strong> plugin is configured to look at the clean CSS (<strong>tidy.css</strong>) and minify the file. Finally, in line 21 to 25, we load the two different plugins and register them.</p>\n<p>All you need to do now is run Grunt.</p>\n<h2>Remove that dirty CSS!</h2>\n<p>In the <strong>Node.js</strong> command prompt, navigate to the folder that contains your source files. This should also be the same folder that contains a <strong>Gruntfile.js</strong> with the configuration settings for the project.</p>\n<img src=\"running-grunt.gif\" alt=\"Running Grunt and UnCSS - Node.js\">\n<p>Once the files have been run through the plugins, the clean, minified CSS will be created in the folder that you specified in the gruntfile. The resulting CSS file that is created comes in at a tidy <strong>5 KB</strong>, making a saving of <strong>119 KB</strong>. That is a saving of over <strong>95%</strong>!</p>\n<p>As you can see, a simple change using this tool can make a significant difference to your page load times. Its a great tool that is packed with useful plugins - it is definitely worth using it on your next project.</p>\n<h2>Further Reading</h2>\n<p>Check out the other <a href=\"http://gruntjs.com/plugins\" target=\"_blank\" rel=\"noopener noreferrer\">great plugins</a> available for Grunt, as well as the <a href=\"https://github.com/addyosmani/grunt-uncss\" target=\"_blank\" rel=\"noopener noreferrer\">UnCSS project on Github</a>. For another detailed installation guide - <a href=\"http://xdamman.com/website-optimization-grunt-uncss\" target=\"_blank\" rel=\"noopener noreferrer\">try this link</a>.</p>\n"}

