	module.exports = {"layout":"post","title":"Setup a free custom domain email on Yandex","description":null,"date":"2014-08-15 23:12:04 +0530","category":"Webdevelopment","tags":["mail","free","yandex"],"imagefeature":null,"body":"<p>Google has put an end to thier free custom domain email service since a long time ago, and finally <a href=\"http://www.pcworld.com/article/2142724/microsoft-ends-free-custom-email-domain-support-for-outlook-com.html\" target=\"_blank\" rel=\"noopener noreferrer\">Microsoft will also do the same thing</a>. Im still using Outlook service for my personal custom email, but I think I must migrate to another service that is still free of charge.</p>\n<p>There are many email services other than Gmail and Outlook with the same quality, one of them is Yandex.Mail.</p>\n<p>Yandex is a Russian based company, many people call it as the Google of Russia because it is also a search engine founded in 1997, and it has the same services like maps, mail, translation, cloud storage, and many more. Free email with @yandex.com available in English, but if you want to setup an email with a custom domain, its available only in Russian. :D</p>\n<blockquote>\n<p>Yandex offers 1000 email accounts with your own domain for free.</p>\n</blockquote>\n<p>Basically, the custom domain email settings are same on all services, but let me help you to setup your custom email on Yandex.Mail step by step.</p>\n<h2>Lets get started!</h2>\n<h4>Update</h4>\n<p>Yandex has a setup page in English!  Just go to <a href=\"https://domain.yandex.com/domains_add/\" target=\"_blank\" rel=\"noopener noreferrer\">https://domain.yandex.com/domains_add/</a> and setup your domain in minutes.  Enjoy!</p>\n<p><strong>1. Create a Yandex account</strong></p>\n<p>You can register an email account on <a href=\"http://mail.yandex.com/\" target=\"_blank\" rel=\"noopener noreferrer\">http://mail.yandex.com/</a>. It is in English, so it must be easy.</p>\n<p><strong>2. Sign in to Yandex domain admin</strong></p>\n<p>Go to <a href=\"https://pdd.yandex.ru/domains_add/\" target=\"_blank\" rel=\"noopener noreferrer\">https://pdd.yandex.ru/domains_add/</a> and sign in with your Yandex account. Tips: just write your username and password, then hit Enter!</p>\n<figure>\n  <a href=\"yandex_1.jpg\" target=\"_blank\"><img width=\"700\" src=\"yandex_1.jpg\"></a>\n</figure>\n<p><strong>3 . Register your domain</strong></p>\n<p>Insert your domain and hit <strong> </strong> button. It means <strong>connect domain</strong>.</p>\n<figure>\n  <a href=\"yandex_2.jpg\" target=\"_blank\"><img width=\"700\" src=\"yandex_2.jpg\"></a>\n</figure>\n<p><strong>4 . Verify domain ownership</strong></p>\n<p>You dont need to change the DNS server, I think the fastest way is by adding a CNAME record to your domain. There are two important values in the red boxes.</p>\n<figure>\n  <a href=\"yandex_3.jpg\" target=\"_blank\"><img width=\"700\" src=\"yandex_3.jpg\"></a>\n</figure>\n<p>Go to the domain admin on your registrar and add the CNAME record, Im using Namecheap for the example.</p>\n<figure>\n  <a href=\"yandex_4.jpg\" target=\"_blank\"><img width=\"700\" src=\"yandex_4.jpg\"></a>\n</figure>\n<p><strong>5 . Setup MX record</strong></p>\n<p>Create a MX record from your domain admin with the following values:</p>\n<blockquote>\n<p>Host Name: @<br>\nServer: <a href=\"http://mx.yandex.ru\" target=\"_blank\" rel=\"noopener noreferrer\">mx.yandex.ru</a>. (ends with dot (.))<br>\nMail type: MX<br>\nPriority : 10</p>\n</blockquote>\n<figure>\n  <a href=\"yandex_5.jpg\" target=\"_blank\"><img width=\"700\" src=\"yandex_5.jpg\"></a>\n <a href=\"yandex_6.jpg\" target=\"_blank\"><img width=\"700\" src=\"yandex_6.jpg\"></a>\n</figure>\n<p><strong>6 . Create an account on your custom email</strong></p>\n<p>Yup, one more step and the setup process will be finished. Now you can create an email account with your own domain, you can create up to 1000 account!</p>\n<figure>\n  <a href=\"yandex_7.jpg\" target=\"_blank\"><img width=\"700\" src=\"yandex_7.jpg\"></a>\n</figure>  \n<p>If you dont get any problem, just go to <a href=\"https://mail.yandex.com/for/yourdomain.com\" target=\"_blank\" rel=\"noopener noreferrer\">https://mail.yandex.com/for/yourdomain.com</a></p>\n<p><strong>7 . IMAP , POP3 and SMTP Config</strong></p>\n<p>Use the following IMAP , POP3 and SMTP config for custom email client:</p>\n<p><strong>IMAP</strong></p>\n<blockquote>\n<p>Server: <a href=\"http://imap.yandex.com\" target=\"_blank\" rel=\"noopener noreferrer\">imap.yandex.com</a><br>\nPort: 993 with SSL enebled</p>\n</blockquote>\n<p><strong>POP3</strong></p>\n<blockquote>\n<p>Server: <a href=\"http://pop.yandex.com\" target=\"_blank\" rel=\"noopener noreferrer\">pop.yandex.com</a><br>\nPort: 995 with SSL enebled</p>\n</blockquote>\n<p><strong>SMTP</strong></p>\n<blockquote>\n<p>Server: <a href=\"http://smtp.yandex.com\" target=\"_blank\" rel=\"noopener noreferrer\">smtp.yandex.com</a><br>\nPort: 465 with SSL enabled</p>\n</blockquote>\n<p><strong>8 . Add New Accounts</strong></p>\n<p>Just go to <a href=\"https://pdd.yandex.ru/domain/yourdomain.com/?master=yes\" target=\"_blank\" rel=\"noopener noreferrer\">https://pdd.yandex.ru/domain/yourdomain.com/?master=yes</a> and add accounts.</p>\n<figure>\n  <a href=\"yandex_8.jpg\" target=\"_blank\"><img width=\"\" src=\"yandex_8.jpg\"></a>\n</figure>  "}

