	module.exports = {"layout":"post","title":"Why Jekyll?","description":null,"headline":null,"date":"2014-08-09 23:59:46 +0530","category":"Webdevelopment","body":"<p>Well, I guess its quite obvious that I really enjoy working with Jekyll  with good reasons  so let me just briefly suggest some of the benefits of using a static site generator in favor of a heavy dynamic, database driven content management system.</p>\n<ul>\n<li>\n<p><strong>Speed</strong>: Static HTML pages are small, lightweight and perform blazingly fast. Youll notice the difference.</p>\n</li>\n<li>\n<p><strong>Version Control</strong>: Contents and files remain on your computer. Perform backups or keep track of changes via GIT or SVN  <em>its quite easy without a database.</em></p>\n</li>\n<li>\n<p><strong>Flexibility</strong>: Ruby and Liquid  <em>Jekylls foundation</em>  empower you to easily extend its functionality. Or just use one of <a href=\"http://jekyllrb.com/docs/plugins/\" target=\"_blank\" rel=\"noopener noreferrer\">many plugins</a> created by a striving community.</p>\n</li>\n<li>\n<p><strong>Security</strong>: Anything youll ever put on your server are static files  chances are good that <em>youll never need to worry</em> about security concerns anymore.</p>\n</li>\n</ul>\n<p>If that still doesnt convince you to at least give it a try, Id like to encourage you to head over to the Jekyll website and see it for yourself.</p>\n<p><strong>Heck, its that good</strong>  I even wrote all of the guides for the theme documentation with it!</p>\n<p>Enjoy!</p>\n"}

