	module.exports = {"layout":"post","title":"WooCommerce : Paypal for INR","description":"Woocommerce Paypal Support For INR (Indian Currency)","date":"2015-06-11 14:03:38 +0530","category":"wordpress","tags":["wordpress","woocommerce","paypal"],"coverimage":"paypal.png","body":"<h3>Gateway Disabled: PayPal does not support your store currency.</h3>\n<p>When we change the currency to INR as store currency in Woocommerce settings then it will result in an error on woocommerce paypal page like this.</p>\n<figure><img src=\"paypal.png\" alt=\"\"></figure>\n<h4>Reason</h4>\n<p>The reason is quite simple because Paypal does not support Indian Currency .</p>\n<h4>Solution</h4>\n<p>The solution is also quite simple.<br>\nFirst We Have to add INR in Paypal supported currencies .<br>\nOpen themes function.php and add following code.</p>\n<pre><code class=\"language-Php\">add_filter( <span class=\"hljs-string\">'woocommerce_paypal_supported_currencies'</span>, <span class=\"hljs-string\">'add_kap_paypal_valid_currency'</span> );\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">add_kap_paypal_valid_currency</span><span class=\"hljs-params\">( $currencies )</span> </span>{\narray_push ( $currencies , <span class=\"hljs-string\">'INR'</span> ); <span class=\"hljs-comment\">/* YOUR CURRENCY */</span>\n<span class=\"hljs-keyword\">return</span> $currencies;\n}\n</code></pre>\n<h4>The Real Thing</h4>\n<p>Paypal still does not support Indian Currency and we cant also make paypal to support Indian Currency. In simple <strong>words Your online store support INR and payapl doesnt The Hook Is</strong> We have to convert currency changes before redirecting to paypal. To do this got to your themes function.php file and add the below code.</p>\n<pre><code class=\"language-Php\"><span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">woocommerce_paypal_args_for_inr</span><span class=\"hljs-params\">($paypal_args)</span></span>{\n    <span class=\"hljs-keyword\">if</span> ( $paypal_args[<span class=\"hljs-string\">'currency_code'</span>] == <span class=\"hljs-string\">'INR'</span>){\n \n        $convert_rate = getFromYahoo();\n \n        $count = <span class=\"hljs-number\">1</span>;\n        <span class=\"hljs-keyword\">while</span>( <span class=\"hljs-keyword\">isset</span>($paypal_args[<span class=\"hljs-string\">'amount_'</span> . $count]) ){\n            $paypal_args[<span class=\"hljs-string\">'amount_'</span> . $count] = round( $paypal_args[<span class=\"hljs-string\">'amount_'</span> . $count] / $convert_rate, <span class=\"hljs-number\">2</span>);\n            $count++;\n        }\n        <span class=\"hljs-keyword\">if</span> (<span class=\"hljs-keyword\">isset</span>($paypal_args[<span class=\"hljs-string\">'tax_cart'</span>])) {\n            $paypal_args[<span class=\"hljs-string\">'tax_cart'</span>] = round($paypal_args[<span class=\"hljs-string\">'tax_cart'</span>] / $convert_rate, <span class=\"hljs-number\">2</span>);\n        }\n\n        <span class=\"hljs-keyword\">if</span> (<span class=\"hljs-keyword\">isset</span>($paypal_args[<span class=\"hljs-string\">'shipping_1'</span>])) {\n            $paypal_args[<span class=\"hljs-string\">'shipping_1'</span>] = round($paypal_args[<span class=\"hljs-string\">'shipping_1'</span>] / $convert_rate, <span class=\"hljs-number\">2</span>);\n        }\n    }\n    <span class=\"hljs-keyword\">return</span> $paypal_args;\n}\n\nadd_filter(<span class=\"hljs-string\">'woocommerce_paypal_args'</span>, <span class=\"hljs-string\">'woocommerce_paypal_args_for_inr'</span>);\n\n<span class=\"hljs-function\"><span class=\"hljs-keyword\">function</span> <span class=\"hljs-title\">getFromYahoo</span><span class=\"hljs-params\">()</span>\n</span>{\n    $from   = <span class=\"hljs-string\">'USD'</span>; <span class=\"hljs-comment\">/*change it to your required currencies */</span>\n    $to     = <span class=\"hljs-string\">'INR'</span>;\n    $url = <span class=\"hljs-string\">'http://finance.yahoo.com/d/quotes.csv?e=.csv&amp;f=sl1d1t1&amp;s='</span>. $from . $to .<span class=\"hljs-string\">'=X'</span>;\n    $handle = @fopen($url, <span class=\"hljs-string\">'r'</span>);\n\n    <span class=\"hljs-keyword\">if</span> ($handle) {\n        $result = fgets($handle, <span class=\"hljs-number\">4096</span>);\n        fclose($handle);\n    }\n    $allData = explode(<span class=\"hljs-string\">','</span>,$result);\n    <span class=\"hljs-keyword\">return</span> $allData[<span class=\"hljs-number\">1</span>];\n}\n</code></pre>\n<h4>The Logic</h4>\n<ul>\n<li>First we are getting all Paypal arguments before redirecting to Paypal by using woocommerce_paypal_args hook.</li>\n<li>After that we are fetching current <strong>INR Rate In USD using Yahoo API.</strong></li>\n<li>After getting conversion rate we are modifying the Paypal Arguments according to USD value.</li>\n<li>Now all done and returning.</li>\n</ul>\n<p><strong>Note  After redirecting to paypal, customer will see all the prices in USD not in INR.</strong></p>\n<h4>Manual Excahnge Rate</h4>\n<p>Replace $convert_rate value with this</p>\n<pre><code><span class=\"hljs-meta\"><span class=\"hljs-meta-keyword\">$convert</span>_rate = 55;</span>\n</code></pre>\n<h3>Final Code</h3>\n<p><a href=\"https://gist.github.com/vikas5914/a13181b0647c2f911d2e\" class=\"\" target=\"_blank\">View on Github</a></p>\n<h3>Fix for Woocommerce 2.3.9 or Above</h3>\n<p><span class=\"alert-box\">Validation error: PayPal currencies do not match (code USD). Order status changed from Pending Payment to On Hold.</span></p>\n<p>To remove this error go to <strong>plugins/woocommerce/includes/gateways/paypal/includes/class-wc-gateway-paypal-ipn-handler.php</strong> and comment two lines (line number : 177 and 178 ) like this</p>\n<pre><code><span class=\"hljs-regexp\">//</span><span class=\"hljs-variable\">$this</span>-&gt;validate_currency( <span class=\"hljs-variable\">$order</span>, <span class=\"hljs-variable\">$posted</span>[<span class=\"hljs-string\">'mc_currency'</span>] );\n<span class=\"hljs-regexp\">//</span><span class=\"hljs-variable\">$this</span>-&gt;validate_amount( <span class=\"hljs-variable\">$order</span>, <span class=\"hljs-variable\">$posted</span>[<span class=\"hljs-string\">'mc_gross'</span>] );\n</code></pre>\n"}

